.recs-summary {
  margin-bottom: 2rem;
}
.recs-summary .summary-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1.5rem;
}
.recs-summary .summary-grid .summary-card {
  background: white;
  border: 1px solid #e5e7eb;
  border-radius: 1rem;
  padding: 1.5rem;
  display: flex;
  align-items: center;
  gap: 1rem;
  transition: all 0.2s ease;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
}
.recs-summary .summary-grid .summary-card:hover {
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  border-color: #d1d5db;
  transform: translateY(-1px);
}
.recs-summary .summary-grid .summary-card .card-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 3rem;
  height: 3rem;
  border-radius: 0.75rem;
  color: white;
  flex-shrink: 0;
}
.recs-summary .summary-grid .summary-card .card-icon.bg-gradient-blue {
  background: linear-gradient(135deg, #3b82f6 0%, #1d4ed8 100%);
}
.recs-summary .summary-grid .summary-card .card-icon.bg-gradient-red {
  background: linear-gradient(135deg, #ef4444 0%, #dc2626 100%);
}
.recs-summary .summary-grid .summary-card .card-icon.bg-gradient-green {
  background: linear-gradient(135deg, #10b981 0%, #059669 100%);
}
.recs-summary .summary-grid .summary-card .card-icon.bg-gradient-orange {
  background: linear-gradient(135deg, #f59e0b 0%, #d97706 100%);
}
.recs-summary .summary-grid .summary-card .card-icon.bg-gradient-purple {
  background: linear-gradient(135deg, #8b5cf6 0%, #7c3aed 100%);
}
.recs-summary .summary-grid .summary-card .card-icon svg {
  width: 1.5rem;
  height: 1.5rem;
}
.recs-summary .summary-grid .summary-card .card-content {
  flex: 1;
  min-width: 0;
}
.recs-summary .summary-grid .summary-card .card-content .card-label {
  display: block;
  font-size: 0.875rem;
  color: #6b7280;
  font-weight: 500;
  margin-bottom: 0.25rem;
}
.recs-summary .summary-grid .summary-card .card-content .card-value {
  display: block;
  font-size: 1.5rem;
  font-weight: 700;
  color: #1f2937;
  line-height: 1.2;
}
.recs-summary .summary-loading {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1.5rem;
}
.recs-summary .summary-loading .loading-skeleton {
  background: white;
  border: 1px solid #e5e7eb;
  border-radius: 1rem;
  padding: 1.5rem;
  display: flex;
  align-items: center;
  gap: 1rem;
}
.recs-summary .summary-loading .loading-skeleton .skeleton-icon {
  width: 3rem;
  height: 3rem;
  background: linear-gradient(90deg, #f3f4f6 25%, #e5e7eb 50%, #f3f4f6 75%);
  background-size: 200% 100%;
  animation: loading 1.5s infinite;
  border-radius: 0.75rem;
}
.recs-summary .summary-loading .loading-skeleton .skeleton-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}
.recs-summary .summary-loading .loading-skeleton .skeleton-content .skeleton-line {
  height: 1rem;
  background: linear-gradient(90deg, #f3f4f6 25%, #e5e7eb 50%, #f3f4f6 75%);
  background-size: 200% 100%;
  animation: loading 1.5s infinite;
  border-radius: 0.25rem;
}
.recs-summary .summary-loading .loading-skeleton .skeleton-content .skeleton-line.w-24 {
  width: 6rem;
}
.recs-summary .summary-loading .loading-skeleton .skeleton-content .skeleton-line.w-16 {
  width: 4rem;
}

@keyframes loading {
  0% {
    background-position: 200% 0;
  }
  100% {
    background-position: -200% 0;
  }
}
@media (max-width: 768px) {
  .recs-summary .summary-grid {
    grid-template-columns: 1fr;
    gap: 1rem;
  }
  .recs-summary .summary-grid .summary-card {
    padding: 1rem;
  }
  .recs-summary .summary-grid .summary-card .card-icon {
    width: 2.5rem;
    height: 2.5rem;
  }
  .recs-summary .summary-grid .summary-card .card-icon svg {
    width: 1.25rem;
    height: 1.25rem;
  }
  .recs-summary .summary-grid .summary-card .card-content .card-value {
    font-size: 1.25rem;
  }
  .recs-summary .summary-loading {
    grid-template-columns: 1fr;
    gap: 1rem;
  }
  .recs-summary .summary-loading .loading-skeleton {
    padding: 1rem;
  }
  .recs-summary .summary-loading .loading-skeleton .skeleton-icon {
    width: 2.5rem;
    height: 2.5rem;
  }
}
